.pizza-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  
    height: 100%;
  
    max-width: 1640px;
    margin: 0 auto;
    margin: 0 50px;
  
  
  }
  .pizza-wrap {
    font-family: 'Montserrat', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
  
    padding:50px 0;
    backdrop-filter: blur(2px) brightness(50%);
    transition: all 0.5s ease;  
  }
  .pizza__title {
    color: white;
  }
  
  .pizza__body {
    
    display: flex;
    flex-direction: column;
    align-items: center;
  
    padding: 10px 40px;
    
    background: rgba( 255, 255, 255, 0.15 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 3.0px );
    -webkit-backdrop-filter: blur( 3.0px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
  
    transition: opacity 0.3s ease;
  }
  
  .pizza__img {
    position: relative;
    width: 250px;
    height: 250px;
  
    border-radius: 50%;
    border: 10px solid #F48E0E;
    background-color: #FEC746;
  }
  .pizza__editor {
    display: flex;
    flex-direction: column;
    gap: 5px;
    width: 300px;
    min-height: 100px;
    padding-left: 0;
    }
  
  
  .editor__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 40px;
  
    list-style-type: none;
  
    background: rgba( 255, 255, 255, 0.05 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 6.5px );
    -webkit-backdrop-filter: blur( 6.5px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
  
    
  }
  .editor__text {
    margin-left: 15px;
    font-weight: 600;
    color: white;
  }
  
  .editor__button {
    width: 30px;
    height: 30px;
    background: none;
    border: none;
    border-radius: 8px;
  
    margin-right: 20px;
  
    background: rgba( 103, 224, 224, 0.30 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 20.0px );
    -webkit-backdrop-filter: blur( 20.0px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    transition: background-color 0.3s ease;
  
  }
  .editor__button:hover {
    background-color: rgba(255, 255, 255, 0.582);
    cursor: pointer;
  }
  .editor__button--false {
    background: url('../svg/close.svg') no-repeat center;
  }
  .editor__button--true {
    background: url('../svg/check-mark.svg') no-repeat center;
  }
  
  .warning {
    height: 40px;
    text-align: center;
    font-weight: 600;
    color: red;
    opacity: 1;
    transition: all 0.7s ease;
  }
  
  .warning-hidden {
    height: 40px;
    text-align: center;
    opacity: 0;
    transition: all 0.7s ease;
    
  }
  
  
  
  
  .ingredient{
    position: absolute;
    top: 0;
    left: 0;
  
    width: 250px;
    height: 250px;
    border-radius: 50%;
  }
  
  
  .cheese {
    background: url('../svg/cheese.svg');
    
  }
  .pepperoni {
    background: url('../svg/pepperoni.svg');
  }
  .jalapeno {
    background: url('../svg/jalapeno.svg');
  }
  .pineapple {
    background: url('../svg/pineapple.svg');
  }
  .onion {
    background: url('../svg/onion.svg');
  }
  .cucumber {
    background: url('../svg/cucumber.svg');
    background-position: 10% 10%;
  }
  .bacon {
    background: url('../svg/bacon.svg');
    background-position: 5% 7%;
  }
  
  .button-disabled {
    opacity: 0.3;
  }
  .text-disable {
    opacity: 0.3;
  }
  .item-disabled {
    background: rgba( 255, 8, 8, 0.30 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 7.0px );
    -webkit-backdrop-filter: blur( 7.0px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    opacity: 0.6;
  }
  .item-active {
    background: rgba(66, 255, 8, 0.3);
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 7.0px );
    -webkit-backdrop-filter: blur( 7.0px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
  }